<template>
  <div class="login">
    <Logintop text="登录">
      <router-link to="/registration" slot="slot">注册</router-link>
    </Logintop>
    <div class="log">
      <Loginname text="登录名" place="手机号码/用户名/邮箱"></Loginname>
      <Loginname text="密码  " place="登录密码"></Loginname>
    </div>
  </div>
</template>
<script>
  import Logintop from './logintop.vue'
  import Loginname from './loginname.vue'
  export default {
    components:{
      Logintop,
      Loginname
    }
  }
</script>
<style scoped>
  .login {
    width: 100%;
  }
  .login > .log{
    width: 100%;
    margin-top: 1rem;
  }
</style>



